import React, { useEffect, useRef } from 'react';
import { select } from 'd3-selection';
import { axisBottom, axisLeft, axisRight } from 'd3-axis';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  axis: {
    '& text': {
      textRendering: 'optimizeLegibility',
      fontSize: '1.5em',
      stroke: 'none',
      fill: 'gray',
    },
    '& path, & line': {
      fill: 'none',
      stroke: 'lightgray',
      strokeWidth: '1px',
      shapeRendering: 'crispEdges',
    },
  },
  xAxis: {
    '& text': {
      textAnchor: 'end',
      transform: 'rotate(-45deg) translate(-8px, 5px)',
    },
  },
  yAxis: {
    '& text': {},
  },
  ySecondAxis: {},
  labels: {
    '& text': {
      transform: 'rotate(-90deg)',
      textAnchor: 'middle',
    },
  },
  legend: {
    '& rect': {
      fill: 'gray',
      stroke: 'none',
    },
  },
}));

const Axes = ({ xScale, yScale, ySecondScale, yScaleTickFormat, ySecondScaleTickFormat, height, width, globalClasses }) => {
  const classes = useStyles();
  const xAxisRef = useRef(null);
  const yAxisRef = useRef(null);
  const ySecondAxisRef = useRef(null);

  useEffect(() => {
    renderXAxis();
    renderYAxis();
    renderSecondYAxis();
  });

  const renderYAxis = () => {
    const yAxis = select(yAxisRef.current);

    yAxis.call(
      axisLeft(yScale)
        .ticks(4)
        .tickFormat(yScaleTickFormat),
    );
    yAxis.select('.domain').remove();
  };

  const renderSecondYAxis = () => {
    const ySecondAxis = select(ySecondAxisRef.current);
    ySecondAxis.call(
      axisRight(ySecondScale)
        .ticks(4)
        .tickFormat(ySecondScaleTickFormat),
    );
    ySecondAxis.select('.domain').remove();
  };

  const renderXAxis = () => {
    const xAxis = select(xAxisRef.current);

    xAxis.call(axisBottom(xScale));
  };

  return (
    <g className={classes.axis}>
      <g className={classes.xAxis} transform={`translate(0,${height})`} ref={xAxisRef} />
      <g className={classes.yAxis} ref={yAxisRef} />
      <g transform={`translate(0, ${height / 2})`} className={classes.labels}>
        <text y="-75px">Gross Margin ($US/ha)</text>
        <text y={width + 75}>Yield (MT/ha)</text>
      </g>
      <g className={classes.ySecondAxis} transform={`translate(${width},0)`} ref={ySecondAxisRef} />
      <g className={classes.legend} transform={`translate(${width / 2},${height + 75})`}>
        <rect y="-10px" width="20px" height="10px" />
        <text x="25px">Gross Margin</text>
        <line
          x1="175px"
          x2="200px"
          y1={-5}
          y2={-5}
          style={{
            fill: 'none',
            stroke: 'black',
            strokeWidth: '2px',
            strokeDasharray: '2,2',
          }}
        />
        <text x="210px">Yield</text>
      </g>
    </g>
  );
};
export default Axes;
