import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { retrieveData } from '../redux/modules/global';
import tabSelector from '../redux/selectors/tab.selector';
import Variety from './Variety';
import Agronomy from './Agronomy';
import Mechanization from './Mechanization';
import Disease from './Disease';
import { DEFAULT_ORDER, DEFAULT_ORDER_BY, DEFAULT_VIEWPORT, OVERALL_BOUNDS } from '../constants';
import Map from './Map';
import { FlyToInterpolator, WebMercatorViewport } from 'react-map-gl';
import CountryMarkers from './Variety/CountryMarkers';
import LocationMarkers from './Variety/LocationMarkers';
import AgronomyMarkers from './Agronomy/AgronomyMarkers';
import MechanizationMarkers from './Mechanization/MechanizationMarkers';
import DiseaseMarkers from './Disease/DiseaseMarkers';

const App = () => {
  const dispatch = useDispatch();
  const isLoading = useSelector(state => state.global.isLoading);
  const selectedTab = useSelector(tabSelector);
  const getData = useCallback(() => dispatch(retrieveData()), [dispatch]);

  const [state, setState] = useState({
    selectedCountry: null,
    selectedLocation: null,
    viewport: DEFAULT_VIEWPORT,
    mapWidth: 900,
    mapHeight: 500,
    orderBy: DEFAULT_ORDER_BY,
    order: DEFAULT_ORDER,
    dropdownIdx: 0,
  });

  useEffect(() => {
    getData();
  }, [getData]);

  const renderSelectedTab = () => {
    switch (selectedTab.id) {
      case 'variety':
        return <Variety state={state} setState={setState} />;
      case 'agronomy':
        return <Agronomy state={state} setState={setState} />;
      case 'mechanization':
        return <Mechanization state={state} setState={setState} />;
      case 'disease':
        return <Disease state={state} setState={setState} />;
      default:
        return null;
    }
  };

  const _onViewportChange = viewport =>
    setState(prevState => ({
      ...prevState,
      viewport,
    }));

  const _goToViewport = ({ longitude, latitude }) => {
    _onViewportChange({
      longitude,
      latitude,
      zoom: 6,
      transitionInterpolator: new FlyToInterpolator({ speed: 1.2 }),
      transitionDuration: 'auto',
    });
  };

  const getViewportFromBounds = bounds =>
    new WebMercatorViewport({
      width: state.mapWidth,
      height: state.mapHeight,
    }).fitBounds(bounds, { padding: 25 });

  const _zoomToMarker = country => {
    const viewport = getViewportFromBounds(country.bbox);
    const { longitude, latitude, zoom } = viewport;
    onClickCountryMarker(country);
    _goToViewport({
      longitude,
      latitude,
      zoom,
    });
  };

  const _goToDefaultViewport = () => {
    const viewport = getViewportFromBounds(OVERALL_BOUNDS);
    setState(prevState => ({
      ...prevState,
      viewport: {
        ...viewport,
        transitionInterpolator: new FlyToInterpolator({ speed: 1.2 }),
      },
      selectedCountry: null,
      selectedLocation: null,
      dropdownIdx: 0,
      orderBy: DEFAULT_ORDER_BY,
      order: DEFAULT_ORDER,
    }));
  };

  const onClickCountryMarker = country => {
    setState(prevState => ({
      ...prevState,
      selectedLocation: null,
      selectedCountry: country,
      dropdownIdx: 0,
      orderBy: DEFAULT_ORDER_BY,
      order: DEFAULT_ORDER,
    }));
  };

  const onClickLocationMarker = location => {
    setState(prevState => ({
      ...prevState,
      selectedLocation: location,
      dropdownIdx: 0,
      orderBy: DEFAULT_ORDER_BY,
      order: DEFAULT_ORDER,
    }));
  };

  const renderMarkers = () => {
    switch (selectedTab.id) {
      case 'variety':
        return (
          <>
            <CountryMarkers onZoom={_zoomToMarker} onClick={onClickCountryMarker} />
            <LocationMarkers selectedCountry={state.selectedCountry} onClick={onClickLocationMarker} />
          </>
        );
      case 'agronomy':
        return <AgronomyMarkers onClick={onClickLocationMarker} />;
      case 'mechanization':
        return <MechanizationMarkers onClick={onClickLocationMarker} />;
      case 'disease':
        return <DiseaseMarkers onClick={onClickLocationMarker} />;
      default:
        return null;
    }
  };

  return (
    <div className="App">
      <Map
        state={state}
        setState={setState}
        _goToDefaultViewport={_goToDefaultViewport}
        _onViewportChange={_onViewportChange}
        onClickCountryMarker={onClickCountryMarker}
        onClickLocationMarker={onClickLocationMarker}
      >
        {renderMarkers()}
      </Map>
      {isLoading ? null : renderSelectedTab()}
    </div>
  );
};

export default App;
