import React from 'react';
import { DEFAULT_ORDER, DEFAULT_ORDER_BY, VARIETY_TABLE_HEAD_ROWS } from '../../../constants';
import { CustomTable } from './CustomTable';
import DownloadLink from './DownloadLink';
import CustomSelect from './CustomSelect';
import { useSelector } from 'react-redux';
import ExtensionReportLink from './../../ExtensionReportLink';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  footer: {
    fontWeight: 'bold',
    marginLeft: '10px',
  },
}));

const Tables = ({ state, setState }) => {
  const classes = useStyles();
  const data = useSelector(state => state.global.data);
  const { selectedLocation, selectedCountry } = state;
  const { countryColors } = data;

  // Handle what we are going to display in each of the two tables
  let varieties = [];
  let acrossSeasonYields = [];
  if (selectedLocation) {
    varieties = data.locationVarietyData[selectedLocation.properties.parent][selectedLocation.properties.id].varieties;
    acrossSeasonYields = data.locationVarietyData[selectedLocation.properties.parent][selectedLocation.properties.id].acrossSeasons;
  } else if (selectedCountry) {
    varieties = data.countryVarietyData[selectedCountry.properties.id].varieties;
    acrossSeasonYields = data.countryVarietyData[selectedCountry.properties.id].acrossSeasons;
  }

  // Get the dropdown values
  const dropdownValues = varieties.map(d => d.season).concat('Across planting seasons');

  const onChange = d => {
    // On change of the dropdown
    const idx = dropdownValues.findIndex(f => d === f);

    // If we are going across planting seasons, set the order by to the first option
    let orderBy = DEFAULT_ORDER_BY;
    if (idx === dropdownValues.length - 1) {
      orderBy = acrossSeasonYields.columns[0].id;
    }

    setState(prevState => ({
      ...prevState,
      orderBy,
      order: DEFAULT_ORDER,
      dropdownIdx: idx,
    }));
  };

  if (!varieties.length) return null;

  const countryName = varieties[0].values[0].Country;

  const selectedDropdownValue = dropdownValues[state.dropdownIdx];

  // Get the correct label for the title header
  const tableTitleLocation = selectedLocation ? selectedLocation.properties.id : selectedCountry.properties.id;

  // Get the varieties for the current season
  const selectedSeasonVarieties = varieties.find(d => d.season === selectedDropdownValue);

  // Get values for the components below
  const isNotAcrossSeasonsSelected = state.dropdownIdx !== dropdownValues.length - 1;
  const values = isNotAcrossSeasonsSelected ? selectedSeasonVarieties.values : acrossSeasonYields.rows;
  const columns = isNotAcrossSeasonsSelected ? VARIETY_TABLE_HEAD_ROWS : acrossSeasonYields.columns;

  return (
    <div id="tables">
      <div className="table__header">
        <div className="title">
          Top Performing Varieties in&nbsp;
          <span style={{ color: countryColors.get(selectedCountry.properties.country) }}>{tableTitleLocation}</span>&nbsp;
          {!selectedLocation && 'Across Locations'}
        </div>
        <div className="table__header__left-container">
          <CustomSelect items={dropdownValues} selectedItem={selectedDropdownValue} onChange={onChange} />
          {isNotAcrossSeasonsSelected && <ExtensionReportLink extensionReportUrl={selectedSeasonVarieties.values[0].Wix_URL || '#'} />}
          <DownloadLink
            data={values}
            countryName={countryName}
            fileName={`${tableTitleLocation}-${selectedDropdownValue}`}
            headRows={columns}
          />
        </div>
      </div>
      <CustomTable data={values} headRows={columns} countryName={countryName} state={state} setState={setState} />
      <p className={classes.footer}>
        *Means across environments do not always match means calculated using values from individuals environments because of how missing
        data were handled in the analysis.
      </p>
    </div>
  );
};

export default Tables;
