import { initialState } from '../state/global';
import { getData } from '../api';
import { ascending, descending } from 'd3-array';
import { nest } from 'd3-collection';
import { scaleOrdinal } from 'd3-scale';
import * as turf from '@turf/turf';
import { schemeCategory10 } from 'd3-scale-chromatic';
import kebabCase from 'lodash.kebabcase';

const colors = scaleOrdinal(schemeCategory10);
const YIELD_PROPERTY = 'Grain Yield (ton/ha)';

// Actions
const TOGGLE_LOADING_ICON = 'TOGGLE_LOADING_ICON';
const UPDATE_DATA = 'UPDATE_DATA';
const UPDATE_SELECTED_TAB = 'UPDATE_SELECTED_TAB';

const formatData = data => {
  // Process the data points for displaying on the map and tables
  // Break apart the results
  let { country, location, agronomy, mechanization, disease } = data;

  // Keep track of all markers for overall bounding box
  const allLocationMarkers = { type: 'FeatureCollection', features: [] };

  // Keep track of country colors
  let countryColors = new Map();

  // Get country markers
  const countryMarkers = [];
  for (let row of country) {
    if (!countryMarkers.find(d => d.properties.id === row.Country)) {
      countryColors.set(row.Country, colors(countryColors.size));

      const feature = {
        type: 'Feature',
        geometry: {
          type: 'Point',
          coordinates: [+row.Long, +row.Lat],
        },
        properties: {
          id: row.Country,
          country: row.Country,
        },
      };
      countryMarkers.push(feature);
      allLocationMarkers.features.push(feature);
    }
  }

  // Process country data into format for tables (and location markers at the same time)
  const countryOutput = {};
  const locationMarkers = {};
  for (let countryMarker of countryMarkers) {
    const { id } = countryMarker.properties;
    const countryData = country.filter(d => d.Country === id).filter(d => !isNaN(+d[YIELD_PROPERTY]));

    // Find all seasons and the matching data points
    const allSeasons = Array.from(new Set(countryData.map(d => d.Season))).sort(descending);

    // Get the yields across planting seasons and format into same format as varieties
    const nestedData = nest()
      .key(d => d.Entry)
      .entries(countryData)
      .map(d => {
        const obj = {};
        for (let i = 0; i < allSeasons.length; i++) {
          const season = allSeasons[i];
          const matchedRow = d.values.find(f => f.Season === season);
          obj[season] = matchedRow ? +matchedRow[YIELD_PROPERTY] : NaN;
        }
        return {
          key: d.key,
          ...obj,
        };
      });

    countryOutput[id] = {
      varieties: allSeasons.map(season => {
        return {
          season: `${season} Field Season`,
          values: countryData.filter(d => d.Season === season).sort((a, b) => descending(a[YIELD_PROPERTY], b[YIELD_PROPERTY])),
        };
      }),
      acrossSeasons: {
        columns: [
          {
            label: `${id} - Grain Yield (ton/ha)`,
            accessor: d => d.key,
            id: kebabCase(`${id} - Grain Yield (ton/ha)`),
          },
        ].concat(
          allSeasons
            .sort(ascending)
            .map(label => ({
              label,
              accessor: d => +d[label],
            }))
            .map(d => ({
              ...d,
              id: kebabCase(d.label),
            })),
        ),
        rows: nestedData,
      },
    };

    // Calculate location markers
    const locationData = location.filter(d => d.Country === id);
    const markers = [];
    for (let row of locationData) {
      if (!markers.find(d => d.properties.id === row.Location)) {
        const feature = {
          type: 'Feature',
          geometry: {
            type: 'Point',
            coordinates: [+row.Long, +row.Lat],
          },
          properties: {
            id: row.Location,
            country: row.Country,
            parent: id,
          },
        };
        markers.push(feature);
        allLocationMarkers.features.push(feature);
      }
    }
    locationMarkers[id] = markers;
  }

  // Retrieve bounding box of all location markers within each country for country zooming on map
  for (let countryMarker of countryMarkers) {
    const selectedCountryMarker = countryMarker.properties.id;
    const featureCollection = {
      type: 'FeatureCollection',
      features: locationMarkers[selectedCountryMarker].concat([countryMarker]),
    };
    const bbox = turf.bbox(featureCollection);
    countryMarker.bbox = [
      [bbox[0], bbox[1]],
      [bbox[2], bbox[3]],
    ];
  }

  // Get overall bounding box
  const overallBoundingBox = turf.bbox(allLocationMarkers);

  // Process location data into format for tables
  const locationOutput = {};
  for (let countryName of Object.keys(locationMarkers)) {
    const locationsByCountry = locationMarkers[countryName];
    locationOutput[countryName] = {};
    for (let countryLocation of locationsByCountry) {
      const locationDataPointsByLocation = location
        .filter(d => d.Country === countryName && d.Location === countryLocation.properties.id)
        .filter(d => !isNaN(+d[YIELD_PROPERTY]));

      // Find all seasons and the matching data points
      const allSeasons = Array.from(new Set(locationDataPointsByLocation.map(d => d.Season)));

      // Get the top performing varieties for the current location and country (greater than or equal to 2 plantings)
      const nestedLocationData = nest()
        .key(d => d.Entry)
        .entries(locationDataPointsByLocation)
        .map(d => {
          const obj = {};
          for (let i = 0; i < allSeasons.length; i++) {
            const season = allSeasons[i];
            const matchedRow = d.values.find(f => f.Season === season);
            obj[season] = matchedRow ? +matchedRow[YIELD_PROPERTY] : NaN;
          }
          return {
            key: d.key,
            ...obj,
          };
        });

      locationOutput[countryName][countryLocation.properties.id] = {
        varieties: allSeasons.map(season => {
          return {
            season: `${season} Field Season`,
            values: locationDataPointsByLocation
              .filter(d => d.Season === season)
              .sort((a, b) => descending(a[YIELD_PROPERTY], b[YIELD_PROPERTY])),
          };
        }),
        acrossSeasons: {
          columns: [
            {
              label: `${countryLocation.properties.id} - Grain Yield (ton/ha)`,
              accessor: d => d.key,
              id: kebabCase(`${countryLocation.properties.id} - Grain Yield (ton/ha)`),
            },
          ].concat(
            allSeasons
              .sort(ascending)
              .map(label => ({
                label,
                accessor: d => +d[label],
              }))
              .map(d => ({
                ...d,
                id: kebabCase(d.label),
              })),
          ),
          rows: nestedLocationData,
        },
      };
    }
  }

  // Calculate agronomy markers
  const agronomyMarkers = [];
  const agronomyIdProperty = 'Location';
  for (let row of agronomy) {
    const country = row.Country.trim();
    if (!agronomyMarkers.find(d => d.properties.id === row[agronomyIdProperty])) {
      if (!countryColors.has(country)) {
        countryColors.set(country, colors(countryColors.size));
      }
      const feature = {
        type: 'Feature',
        geometry: {
          type: 'Point',
          coordinates: [+row.Long, +row.Lat],
        },
        properties: {
          id: row[agronomyIdProperty],
          country,
          values: agronomy.filter(d => d[agronomyIdProperty] === row[agronomyIdProperty]),
        },
      };
      agronomyMarkers.push(feature);
    }
  }

  // Calculate mechanization markers
  const mechanizationMarkers = [];
  for (let row of mechanization) {
    const country = row.Country.trim();
    if (!mechanizationMarkers.find(d => d.properties.country === country)) {
      if (!countryColors.has(country)) {
        countryColors.set(country, colors(countryColors.size));
      }
      const feature = {
        type: 'Feature',
        geometry: {
          type: 'Point',
          coordinates: [+row.Long, +row.Lat],
        },
        properties: {
          id: country,
          country,
          values: mechanization.filter(d => d.Country.trim() === country),
        },
      };
      mechanizationMarkers.push(feature);
    }
  }

  // Calculate disease markers
  const diseaseMarkers = [];
  for (let row of disease) {
    const feature = {
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: [+row.Long, +row.Lat],
      },
      properties: {
        id: `${(+row.Long).toFixed(4)},${(+row.Lat).toFixed(4)}-${row.Location}-${row.Season}`,
        severity: parseInt(row['Average Severity']),
        location: row['Location'],
        incidence: row['Incidence'],
        maximum: row['Max'],
        country: row['Country'],
      },
    };
    diseaseMarkers.push(feature);
  }

  return {
    boundingBox: [
      [overallBoundingBox[0], overallBoundingBox[1]],
      [overallBoundingBox[2], overallBoundingBox[3]],
    ],
    countryMarkers,
    locationMarkers,
    agronomyMarkers,
    mechanizationMarkers,
    diseaseMarkers,
    locationVarietyData: locationOutput,
    countryVarietyData: countryOutput,
    countryColors,
  };
};

// Reducer
export default function reducer(state = initialState, action) {
  switch (action.type) {
    case UPDATE_DATA:
      return {
        ...state,
        data: formatData(action.data),
      };
    case TOGGLE_LOADING_ICON:
      return {
        ...state,
        isLoading: action.x,
      };
    case UPDATE_SELECTED_TAB:
      return {
        ...state,
        tabs: state.tabs.map(tab => ({
          ...tab,
          isSelected: tab.id === action.id,
        })),
      };
    default:
      return state;
  }
}

function toggleLoadingIcon(x) {
  return {
    type: TOGGLE_LOADING_ICON,
    x,
  };
}

export function updateSelectedTab(id) {
  return {
    type: UPDATE_SELECTED_TAB,
    id,
  };
}

export function retrieveData() {
  return async dispatch => {
    dispatch(toggleLoadingIcon(true));

    try {
      const data = await getData();
      dispatch({
        type: UPDATE_DATA,
        data,
      });
    } catch (err) {
      console.log(err);
    }

    dispatch(toggleLoadingIcon(false));
  };
}
