import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';

const EnhancedTableHeader = ({ classes, headRows, state, countryName, onRequestSort }) => {
  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow className={classes.HeaderRow}>
        {headRows.map(row => {
          let label = row.label;
          if (row.hasOwnProperty('insertCountryName')) {
            label = row.label.replace('countryName', countryName);
          }
          return (
            <TableCell
              className={classes.Header}
              key={row.id}
              align={'left'}
              padding={row.disablePadding ? 'none' : 'default'}
              sortDirection={state.orderBy === row.id ? state.order : false}
            >
              <TableSortLabel
                // className={orderBy === row.id ? classes.tableSortLabelActive : ''}
                active={state.orderBy === row.id}
                direction={state.orderBy === row.id ? state.order : 'asc'}
                onClick={createSortHandler(row.id)}
              >
                {label}
                {state.orderBy === row.id ? (
                  <span className={classes.visuallyHidden}>{state.order === 'desc' ? 'sorted descending' : 'sorted ascending'}</span>
                ) : null}
              </TableSortLabel>
            </TableCell>
          );
        })}
      </TableRow>
    </TableHead>
  );
};
export default EnhancedTableHeader;
