import * as React from 'react';
import { Marker } from 'react-map-gl';
import { useSelector } from 'react-redux';

const CountryMarkers = ({ onZoom }) => {
  const data = useSelector(state => state.global.data);
  const countryColors = data.countryColors;

  return (
    <>
      {data.countryMarkers.map(countryMarker => {
        const [longitude, latitude] = countryMarker.geometry.coordinates;
        const { id, country } = countryMarker.properties;
        return (
          <Marker key={`country-marker-${id}`} longitude={longitude} latitude={latitude}>
            <svg
              className="e-marker"
              style={{
                cursor: 'pointer',
                fill: countryColors.get(country),
                stroke: 'none',
                transform: `translate(${-36 / 2}px,${-36}px)`,
              }}
              viewBox="0 0 72 130.7"
              width="36"
              onClick={() => onZoom(countryMarker)}
            >
              <path
                className="e-marker__circle"
                d="M36,97.4c15,0,27.3-12.2,27.3-27.3c0-15-12.2-27.3-27.3-27.3S8.7,55.1,8.7,70.2S21,97.4,36,97.4z"
              />
              <path
                className="e-marker__icon e-marker__icon--city"
                d="M41.1,68.7V58.5L36,53.4l-5.1,5.1v3.4H20.8v23.7h30.5V68.7H41.1z M27.5,82.2h-3.4v-3.4h3.4V82.2z M27.5,75.4h-3.4
	V72h3.4V75.4z M27.5,68.7h-3.4v-3.4h3.4V68.7z M37.7,82.2h-3.4v-3.4h3.4V82.2z M37.7,75.4h-3.4V72h3.4V75.4z M37.7,68.7h-3.4v-3.4
	h3.4V68.7z M37.7,61.9h-3.4v-3.4h3.4V61.9z M47.9,82.2h-3.4v-3.4h3.4V82.2z M47.9,75.4h-3.4V72h3.4V75.4z"
              />
            </svg>
          </Marker>
        );
      })}
    </>
  );
};

export default CountryMarkers;
