import * as React from 'react';
import MapGL from 'react-map-gl';
import { Component } from 'react';
import HomeIcon from '@material-ui/icons/Home';
import Message from './Message';
import { connect } from 'react-redux';
import { updateSelectedTab } from '../../redux/modules/global';
import { IMAGE_LOOKUP } from '../../constants';

class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      messageClass: 'visible',
    };
    this.mapRef = React.createRef();
  }

  componentDidMount() {
    const { offsetWidth, offsetHeight } = this.mapRef.current;
    this.props.setState(prevState => ({
      ...prevState,
      mapWidth: offsetWidth,
      mapHeight: offsetHeight,
    }));
    this.props._goToDefaultViewport();
    setTimeout(() => {
      setTimeout(() => {
        this.setState({
          messageClass: 'hidden',
        });
      }, 1000);
      this.setState({
        messageClass: 'exiting',
      });
    }, 5000);
  }

  render() {
    const { state, _onViewportChange, _goToDefaultViewport, tabs, children, onTabClick } = this.props;
    const { messageClass } = this.state;

    return (
      <div id="map" ref={this.mapRef}>
        {messageClass !== 'hidden' && <Message messageClass={messageClass} />}
        <div className="toggle__button__container">
          {tabs.map(tab => {
            const imageUrls = IMAGE_LOOKUP.get(tab.id);
            const imageUrl = tab.isSelected ? imageUrls[1] : imageUrls[0];
            return (
              <img
                className="icon"
                key={`tab-button-for-${tab.id}`}
                onClick={() => {
                  _goToDefaultViewport();
                  onTabClick(tab.id);
                }}
                src={imageUrl}
                alt={`Toggle to the ${tab.id} view`}
              />
            );
          })}
        </div>
        <div className="icon__container">
          <HomeIcon className="icon" fontSize="default" onClick={() => _goToDefaultViewport()} />
        </div>
        <MapGL
          {...state.viewport}
          width="100%"
          height="100%"
          mapStyle="mapbox://styles/mapbox/light-v9"
          onViewportChange={_onViewportChange}
          dragRotate={false}
          mapboxApiAccessToken="pk.eyJ1IjoiZ3JhcGhpY2FjeSIsImEiOiJja2IxMnpiMG0wZzl2MnptcGQ2bGpldGZ1In0.h0CLJxaPaDwWJosU-3TH0w"
        >
          {children}
        </MapGL>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return { tabs: state.global.tabs };
}

const mapDispatchToProps = dispatch => {
  return {
    onTabClick: id => dispatch(updateSelectedTab(id)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Map);
