import React, { useState } from 'react';
import BaseChart from './BaseChart';
import { makeStyles } from '@material-ui/core/styles';
import { useSelector } from 'react-redux';
import TreatmentChart from './TreatmentChart';
import ExtensionReportLink from '../ExtensionReportLink';
import CustomSelect from '../Variety/Tables/CustomSelect';

const useStyles = makeStyles(() => ({
  agronomyContainer: {
    height: '100%',
  },
  containerHeader: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    margin: '10px',
    backgroundColor: 'white',
  },
  title: {
    textAlign: 'left',
    fontSize: '20px',
    color: '#4d4d4d',
  },
  agronomyChart: {
    width: '100%',
    height: 'calc(var(--table-height) - 50px)',
  },
  leftContainer: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    '& :first-child': {
      marginRight: '10px',
    },
  },
}));

const Agronomy = ({ state }) => {
  const classes = useStyles();
  const [dropdownIdx, setDropdownIdx] = useState(0);
  const selectedLocation = state.selectedLocation;
  const data = useSelector(state => state.global.data);
  const countryColors = data.countryColors;

  if (!selectedLocation) return null;

  const { country, id, values } = selectedLocation.properties;

  // Get the dropdown values, as unique seasons
  const dropdownValues = Array.from(new Set(values.map(value => value.Season)));
  const selectedDropdownValue = dropdownValues[dropdownIdx];

  const onChange = d => {
    // On change of the dropdown, update the index of the selected value
    const idx = dropdownValues.findIndex(f => d === f);
    setDropdownIdx(idx);
  };

  return (
    <div className={classes.agronomyContainer}>
      <div className={classes.containerHeader}>
        <div className={classes.title}>
          Treatment Yields for&nbsp;
          <span style={{ color: countryColors.get(country) }}>{id}</span>&nbsp;
        </div>
        <div className={classes.leftContainer}>
          <CustomSelect items={dropdownValues} selectedItem={selectedDropdownValue} onChange={onChange} />
          <ExtensionReportLink extensionReportUrl={values[values.length - 1].Wix_URL || '#'} />
        </div>
      </div>
      <BaseChart className={classes.agronomyChart}>
        <TreatmentChart
          selectedLocation={{
            ...selectedLocation,
            properties: {
              ...selectedLocation.properties,
              values: values.filter(value => value.Season === selectedDropdownValue),
            },
          }}
        />
      </BaseChart>
    </div>
  );
};

export default Agronomy;
