import React from 'react';
import { max } from 'd3-array';
import { scaleBand, scaleLinear } from 'd3-scale';
import { line as d3_line } from 'd3-shape';
import Axes from './Axes';
import { makeStyles } from '@material-ui/core/styles';
import { format } from 'd3-format';

const useStyles = makeStyles(theme => ({
  line: {
    fill: 'none',
    stroke: 'black',
    strokeWidth: '1px',
    strokeDasharray: '2,2',
  },
}));

const TreatmentChart = ({ innerWidth, innerHeight, selectedLocation }) => {
  const classes = useStyles();
  const values = selectedLocation.properties.values;

  // Set up scales
  const xAccessor = d => d['Treatment'];
  const xScale = scaleBand()
    .range([0, innerWidth])
    .padding(0.1)
    .domain(values.map(xAccessor));

  const yAccessor = d => Math.max(+d['Gross Margin (USD/ha)'], 0);
  const yScale = scaleLinear()
    .range([innerHeight, 0])
    .domain([0, max(values, yAccessor)]);

  const yRightAccessor = d => +d['Yield (MT/hectare)'];
  const yRightScale = scaleLinear()
    .range([innerHeight, 0])
    .domain([0, max(values, yRightAccessor)]);

  // Set up line generator
  const line = d3_line()
    .x(d => xScale(xAccessor(d)) + xScale.bandwidth() / 2)
    .y(d => yRightScale(yRightAccessor(d)));

  return (
    <g className="agronomy-chart">
      <g className="bar-group">
        {values.map(agronomyMarker => {
          return (
            <rect
              key={`bar-for-${xAccessor(agronomyMarker)}`}
              width={xScale.bandwidth()}
              height={yScale(0) - yScale(yAccessor(agronomyMarker))}
              x={xScale(xAccessor(agronomyMarker))}
              y={yScale(yAccessor(agronomyMarker))}
              style={{
                fill: agronomyMarker['Hex-color'],
                stroke: agronomyMarker['Hex-color'] === 'white' ? 'black' : 'none',
              }}
            />
          );
        })}
      </g>
      <g className="line-group">
        <path className={classes.line} d={line(values)} />
      </g>
      <Axes
        height={innerHeight}
        width={innerWidth}
        xScale={xScale}
        yScale={yScale}
        ySecondScale={yRightScale}
        yScaleTickFormat={format('$,.0f')}
        ySecondScaleTickFormat={format('.1f')}
        globalClasses={classes}
      />
    </g>
  );
};

export default TreatmentChart;
