import React, { useState } from 'react';
import { Marker } from 'react-map-gl';
import { useSelector } from 'react-redux';
import { ICON, SIZE } from '../../../constants';

const LocationMarkers = ({ selectedCountry, onClick }) => {
  const data = useSelector(state => state.global.data);
  const [activeLocation, setActiveLocation] = useState(null);
  const locationMarkers = selectedCountry ? data.locationMarkers[selectedCountry.properties.id] : [];
  const countryColors = data.countryColors;

  const onMarkerClick = location => () => {
    onClick(location);
    setActiveLocation(activeLocation && location.properties.id === activeLocation.properties.id ? null : location);
  };

  return locationMarkers.map(location => {
    const [longitude, latitude] = location.geometry.coordinates;
    const { id, country } = location.properties;
    return (
      <Marker key={`location-marker-${id}`} longitude={longitude} latitude={latitude}>
        <svg
          height={SIZE}
          viewBox="0 0 24 24"
          style={{
            cursor: 'pointer',
            fill: countryColors.get(country),
            stroke: activeLocation && location.properties.id === activeLocation.properties.id ? 'white' : 'none',
            strokeWidth: '3px',
            transform: `translate(${-SIZE / 2}px,${-SIZE}px)`,
          }}
          onClick={onMarkerClick(location)}
        >
          <path d={ICON} />
        </svg>
      </Marker>
    );
  });
};

export default LocationMarkers;
