import { json } from 'd3-fetch';

const DATA_URL = 'https://sil-interactive-soybean-map-data-1.s3.amazonaws.com';

export const getData = () => {
  return new Promise(async (resolve, reject) => {
    const files = [
      { name: `${DATA_URL}/country.json`, id: 'country' },
      { name: `${DATA_URL}/location.json`, id: 'location' },
      { name: `${DATA_URL}/agronomy.json`, id: 'agronomy' },
      { name: `${DATA_URL}/mechanization.json`, id: 'mechanization' },
      { name: `${DATA_URL}/disease.json`, id: 'disease' },
    ];
    const output = {};
    for (let file of files) {
      try {
        output[file.id] = await json(file.name);
      } catch (err) {
        return reject(err);
      }
    }
    return resolve(output);
  });
};
