import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { MenuItem, FormControl, Select, InputBase } from '@material-ui/core';

const BootstrapInput = withStyles(theme => ({
  root: {
    'label + &': {
      marginTop: theme.spacing(3),
    },
  },
  input: {
    padding: '6.5px 26px 6.5px 16px',
  },
}))(InputBase);

const useStyles = makeStyles(theme => ({
  formControl: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    minWidth: 200,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  label: {
    '&.Mui-focused': {
      color: '#000',
    },
  },
  select: {
    borderRadius: 5,
    boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.2)',
    '.MuiSelect-select': {
      paddingLeft: theme.spacing(1),
    },
    '&:hover:not(.Mui-disabled):before, &:before': {
      display: 'none',
    },
    '&:after': {
      display: 'none',
    },
  },
}));

const CustomSelect = ({ items, selectedItem, onChange }) => {
  const classes = useStyles();

  return (
    <FormControl className={classes.formControl}>
      <Select
        labelId="planting-season-label"
        id="planting-season"
        value={selectedItem}
        onChange={evt => onChange(evt.target.value)}
        className={classes.select}
        input={<BootstrapInput />}
      >
        {items.map(item => {
          return (
            <MenuItem key={`menu-item-${item}`} value={item}>
              {item}
            </MenuItem>
          );
        })}
      </Select>
    </FormControl>
  );
};
export default CustomSelect;
