import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useSelector } from 'react-redux';
import BioSection from './BioSection';

const useStyles = makeStyles(() => ({
  mechanizationContainer: {
    height: 'auto',
    overflowY: 'auto',
    position: 'relative',
    margin: '10px',
  },
  containerHeader: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    backgroundColor: 'white',
    top: 0,
    position: 'sticky',
    paddingBottom: '10px',
  },
  title: {
    textAlign: 'left',
    fontSize: '20px',
    color: '#4d4d4d',
  },
  bodyContainer: {
    width: '100%',
    display: 'flex',
    flexWrap: 'wrap',
    margin: '10px',
  },
  header: {
    fontWeight: 'bold',
    margin: '10px 0',
  },
}));

const Mechanization = ({ state }) => {
  const classes = useStyles();
  const selectedLocation = state.selectedLocation;
  const data = useSelector(state => state.global.data);
  const mechanizationMarkers = data.mechanizationMarkers;
  const countryColors = data.countryColors;

  if (!selectedLocation) return null;

  const { country } = selectedLocation.properties;
  const mechanizationMarker = mechanizationMarkers.find(d => {
    return d.properties.country === selectedLocation.properties.country;
  });

  return (
    <div className={classes.mechanizationContainer}>
      <div className={classes.containerHeader}>
        <div className={classes.title}>
          Mechanization Operator List for&nbsp;
          <span style={{ color: countryColors.get(country) }}>{country}</span>&nbsp;
        </div>
      </div>
      <div className={classes.bodyContainer}>
        {mechanizationMarker.properties.values.map((operator, i) => {
          const id = operator.Name;
          return <BioSection key={`bio-section-for-${id}`} operator={operator} />;
        })}
      </div>
    </div>
  );
};

export default Mechanization;
