import { useCallback, useState, useLayoutEffect } from 'react';
import debounce from 'lodash.debounce';

function descendingComparator(a, b, orderByAccessor) {
  if (orderByAccessor(b) < orderByAccessor(a)) {
    return -1;
  }
  if (orderByAccessor(b) > orderByAccessor(a)) {
    return 1;
  }
  return 0;
}

export function getComparator(order, orderByAccessor) {
  return order === 'desc' ? (a, b) => descendingComparator(a, b, orderByAccessor) : (a, b) => -descendingComparator(a, b, orderByAccessor);
}

export function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

export const getExportData = ({ data, headRows, countryName }) => {
  headRows = [{ label: 'Country', accessor: _ => countryName }].concat(headRows);
  return [
    headRows.map(headRow => {
      let label = headRow.label;
      if (headRow.hasOwnProperty('insertCountryName')) {
        label = headRow.label.replace('countryName', countryName);
      }
      return label;
    }),
  ].concat(
    data.map(row =>
      headRows.map(headRow => {
        return headRow.accessor(row);
      }),
    ),
  );
};

function getDimensionObject(node) {
  const rect = node.getBoundingClientRect();

  return {
    width: rect.width,
    height: rect.height,
    top: 'x' in rect ? rect.x : rect.top,
    left: 'y' in rect ? rect.y : rect.left,
    x: 'x' in rect ? rect.x : rect.left,
    y: 'y' in rect ? rect.y : rect.top,
    right: rect.right,
    bottom: rect.bottom,
  };
}

export const useDimensions = initialDimensions => {
  const [dimensions, setDimensions] = useState(initialDimensions);
  const [node, setNode] = useState(null);

  const ref = useCallback(node => {
    setNode(node);
  }, []);

  useLayoutEffect(() => {
    if (node) {
      const measure = debounce(
        () => window.requestAnimationFrame(() => setDimensions({ ...getDimensionObject(node), isResized: true })),
        250,
      );
      measure();

      window.addEventListener('resize', measure);

      return () => {
        window.removeEventListener('resize', measure);
      };
    }
  }, [node]);

  return [ref, dimensions, node];
};
