import React, { useState } from 'react';
import { getComparator, stableSort } from '../../../../utils';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import EnhancedTableHeader from '../EnhancedTableHeader';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TablePaginationActions from '../TablePaginationActions';
import { makeStyles } from '@material-ui/core';
import TableContainer from '@material-ui/core/TableContainer';
import { withStyles } from '@material-ui/core/styles';

const useTableStyles = makeStyles(theme => ({
  table: {
    minWidth: 750,
  },
  tableWrapper: {
    overflowX: 'auto',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  HeaderRow: {
    position: 'sticky',
    top: 0,
  },
  Header: {
    backgroundColor: '#719DD0',
    color: '#fff',
    fontSize: '14px',
    fontWeight: 'bold',
    textAlign: 'left',
  },
  TableCell: {
    fontSize: '14px',
    color: '#707070',
  },
  tableSortLabelActive: {
    color: '#fff',
    '&.active': {
      color: '#fff',
    },
    icon: {
      color: '#fff',
    },
  },
  selectedRow: {
    backgroundColor: theme.palette.action.selected,
  },
}));

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const CustomTable = ({ data, headRows, countryName, state, setState }) => {
  const classes = useTableStyles();
  const rowsPerPageOption = [5, 10, 20, 50].filter((d, i) => !i || d <= data.length);
  const [rowsPerPage, setRowsPerPage] = useState(rowsPerPageOption[0]);
  const [page, setPage] = useState(0);

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, data.length - page * rowsPerPage);

  const handleRequestSort = (event, property) => {
    const isAsc = state.orderBy === property && state.order === 'asc';
    setState(prevState => ({
      ...prevState,
      order: isAsc ? 'desc' : 'asc',
      orderBy: property,
    }));
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <TableContainer className="variety" component={Paper}>
      <Table className={classes.table} stickyHeader aria-label="A table of soybean varieties in Africa">
        <EnhancedTableHeader
          classes={classes}
          headRows={headRows}
          state={state}
          countryName={countryName}
          onRequestSort={handleRequestSort}
        />
        <TableBody>
          {stableSort(data, getComparator(state.order, headRows.find(d => d.id === state.orderBy).accessor))
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((row, i) => {
              return (
                <StyledTableRow key={`variety-row-for-${i}`}>
                  {headRows.map(headRow => {
                    let x = headRow.accessor(row);
                    return (
                      <TableCell key={`table-row-for-variety-list-${i}-${headRow.id}`} className={classes.TableCell} align="left">
                        {headRow.hasOwnProperty('formatter') ? headRow.formatter(x).toString() : x.toString()}
                      </TableCell>
                    );
                  })}
                </StyledTableRow>
              );
            })}
          {emptyRows > 0 && (
            <TableRow style={{ height: 48 * emptyRows }}>
              <TableCell colSpan={6} />
            </TableRow>
          )}
        </TableBody>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={rowsPerPageOption}
              count={data.length}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'varieties per page' },
                native: true,
              }}
              backIconButtonProps={{
                'aria-label': 'previous page',
              }}
              nextIconButtonProps={{
                'aria-label': 'next page',
              }}
              onChangePage={handleChangePage}
              onChangeRowsPerPage={handleChangeRowsPerPage}
              ActionsComponent={TablePaginationActions}
            />
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  );
};
export { CustomTable, useTableStyles };
